.dashboard{
    width: 87vw;
    background-color: var(--bgGrey);
    overflow-y: scroll;
    height: 100vh;
}

.dashboard__box{
    width: 14vw;
    background-color: #fff;
}

.dashboard__box p{
    margin-bottom: 4px;
}

.dashboard__created{
    background-color: var(--bgLight);
}

.gc__preview{
    width: 4rem;
    height: 2rem;
    object-fit: contain;
}

.editGameModal{
    width: 42vw;
    /* height: 41vh; */
    background-color: var(--bgLight);
}

.editGameModal form{
    height: 100%;
}

.editGameModal p{
    font-size: 12px;
    margin-bottom: 0;
}

.editLoading{
    width: 12vw;
    height: 11vh;
    background-color: var(--bgLight);
}

.editGame__section img{
    width: 17vw;
    object-fit: contain;
    height: 20vh;
}

.gamelist_changeBtn{
    text-transform: none !important;
}

.dash__table{
    min-height: 50vh;
}

.dash__table img{
    width: 100%;
    height: 35vh;
    margin-bottom: 1rem;
}

.gamesCreated a{
    color: var(--navHover) !important;
}

.gamesCreated a:hover{
    background-color: rgba(0, 0, 0, 0.2) !important;
}

.dashboard__cards{
    margin-top: 3rem;
}

.dashboard__card{
    width: 15.5rem;
    height: 6rem;
    transition: .3s ease-in-out;
}

.dashboard__card svg{
    font-size: 2.5rem;
    margin-bottom: .25rem;
}

.dashboard__card__create{
    background-color: #6081fb4d !important;
    color: #04218B !important;
}

.dashboard__card__create:hover{
    background-color: #476df86c !important;
    color: #04218B !important;
}

.dashboard__card__users{
    background-color: #fc7a1e41 !important;
    color: #663b1d !important;
}

.dashboard__card__users:hover{
    background-color: #fc7a1e69 !important;
    color: #8D3C02 !important;
}

.dashboard__card__payment{
    background-color: #1b998a38 !important;
    color: #0F574E !important;
}

.dashboard__card__payment:hover{
    background-color: #1B998B8c !important;
    color: #124640 !important;
}

.dashboard__card__noti{
    background-color: #ffac0533 !important;
    color: #664400 !important;
}

.dashboard__card__noti:hover{
    background-color: #FFAD058c !important;
    color: #50390b !important;
}
