.dashnav{
    width: 13vw;
    /* background-color: #fefefe; */
    color: #FDFCFD;
    background-color: #050632;
    /* background-image: linear-gradient(316deg, #310e68 0%, #5f0f40 74%); */
    overflow: hidden    ;
    height: 100vh;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

/* 
366BFE
E7E7EA
FDFCFD 
*/

.nav__buttons{
    width: 100%;
    margin-top: 3rem;
}

.nav__buttons a{
    text-align: start !important;
}

.nav__header {
    height: 2.5rem;
    text-align: center;
}

a{
    color: #FDFCFD !important;
    padding: .5rem .75rem;
    border-radius: 4px;
    transition: .2s ease-in-out;
    width: 90%;
}

a:hover{
    background-color: #366BFE !important;
}

nav a svg, nav button svg{
    margin-right: 1rem;
}

.accordion-buttonl, .accordion-item, .accordion-button:not(.collapsed), .accordion-button svg{
    color: #FDFCFD !important;
}
.accordion-item, .accordion-button, .accordion-button:not(.collapsed){
    background-color: transparent;
}

.accordion-button{
    color: #FDFCFD !important;
    text-align: center;
}
.accordion-button::after, .accordion-button:not(.collapsed)::after{
    background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23f5f5f5'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>");
}

.accordion-button:focus{
    box-shadow: none!important;
}

.accordion-button:not(.collapsed){
    background-color: #1A1F45;
}

.accordion-collapse{
    background-color: #1A1F45;
}

.accordion-item {
    border: 0;
}

nav button{
    color: #FDFCFD !important;

}

nav button:hover{
    background-color: #366BFE !important;
}

.signOut-btn:hover{
    background-color: transparent !important;
    color: #e6e6e6 !important;
}

.signOut-btn{
    bottom: 1rem;
}

.nav__links a{
    white-space: nowrap;
    min-width: max-content;
}

.navActive{
    background-color: #366BFE !important;
}

.accordion-body{
    max-height: 40vh;
    overflow: scroll;
}

.accordion-body a{
    font-size: .8rem;
    justify-content: start;
}

.nav__header img{
    width: 100%;
    height: 2rem;
    object-fit: contain;
}

.nav__header{
    position: relative;
}

.nav__header p{
    position: absolute;
    right: 1rem;
    top: 1.5rem;
}