.changeStatusBtn{
    text-transform: none !important;
    border-bottom: 2px solid #000;
}

.status-enable{
    color: #BD1616 !important;
}

.status-enable:hover{
    background-color: #bd16161e !important;
}

.status-disable{
    color: #1E5128 !important;
}
.status-disable:hover{
    background-color: #1E51281e !important;
}

.contest__edit{
    background-color: transparent !important;
    color: #366BFE!important;
}

.contest__result{
    color: #624763 !important;
}

.contest__result:hover, .contest__edit:hover{
    background-color: #366bfe0e !important;
}

.createContest, .editContest{
    background-color: var(--bgLight);
    width: 80vw;
    height: 86vh;
    overflow-y: hidden;
}

.contestform__container {
    height: 66vh;
    overflow-y: scroll;
    border-bottom: 2px solid rgba(0, 0, 0, 0.082);
}

.contestDetail__ai input{
    width: 20vw;
}

.cd__imageInput{
    width: 17vw;
}

.contest__inputs{
    width: 50vw;
}

.contest__image{
    width: 25vw;
}

.contest__image img{
    height: 26vh;
    width: 26vw;
    object-fit: contain;
}

