.cd__prizes input{
    width: 70%;
}

.cd__prizes p{
    margin-bottom: 0;
}

.cd__prizes button{
    margin-bottom: 0 !important;
}

.cd__prize_btn{
    width: 2rem;
    height: 2rem;
}

.prizebox{
    height: 11vh;
    overflow-y: scroll;
    width: 16vw;
}

.cd__prize_btn{
    height: 3.5rem;
    border-radius: 0px 4px 4px 0px !important;
    transform: translateX(-3rem);
}

.prizebox button svg{
    color: #BD1616;
}

.contestTable .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
    border: 0px;
}

.editContest header div:nth-child(2) p{
    font-size: 12px;
    margin-bottom: 6px;
}

.cd__editInput{
    width: 25%;
}

.ct__title{
    width: 27vw;
}

.contest__tablewrapper{
    width: 100%;
    height: 88vh;
    overflow: scroll;
}

.contestTable thead{
    position: sticky;
    top: 0;
    background-color: var(--bgLight);
    z-index: 87;
}

.contestTable tbody{
    /* max-height: 50vh; */
    width: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
}

.contestTable td{
    vertical-align: middle;
}

.cd__submitBar{
    /* box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; */
}

.filter__menuContest{
    width: 36vw;
    /* height: 40vh; */
}

.filters__contest label{
    margin-bottom: 4px;
}

.contest__position{
    background-color: var(--bgLight);
    width: 30vw;
    max-height: 60vh;
    overflow: auto;
    overflow-x: hidden;
}

.contest__position svg{
    color: #BD1616;
}

.cd__prizes p svg{
    color: #366BFE;
}

.cd__prizes p{
    font-size: .9rem;
    margin-bottom: 0
}

.contestform__container .MuiInputLabel-outlined {
    transform: translate(12px, 14px) scale(1) !important;
}

.contestform__container .MuiInputLabel-outlined.MuiInputLabel-shrink {
    transform: translate(12px, -6px) scale(0.75) !important;
}

.contest__tablewrapper td{
    max-width: 17vw;
}