.windraw__request{
    width: 87vw;
    background-color: var(--bgGrey);
    overflow-y: scroll;
    height: 100vh;
}

.widraw__container{
    background-color: var(--bgLight);
    min-height: 70vh;
}


.widraw__buttonActive{
    color: #FDFCFD !important;
    background-color: var(--navHover) !important;
}

.buttonToggler button{
    text-transform: none;
}

.acceptwidrawal{
    color: var(--notDanger) !important;
}

.rejectwidrawal{
    color: var(--danger) !important;
}

.widraw__contents a{
    color: var(--navHover) !important;
}

.widraw__contents a:hover{
    color: #FDFCFD !important;

}