.result{
    width: 87vw;
}

.result__container{
    background-color: var(--bgLight);
    height: 90vh;
}

.resultTable td{
    width: 9vw;
}

.rt-win{
    width: 11vw
}

.resultTable__editable{
    max-width: 11vw !important;
    max-height: 4vh !important;
    overflow: hidden;
    transition: .3s ease-in-out;
    border-bottom: 0  #366BFE transparent;
}

.resultTable__editable:hover{
    cursor: text;
    background-color: #e7e7e73d;
    border-bottom: 1px #366BFE solid;
}

.resultTable__editable:focus{
    border-bottom: 1px #366BFE solid;
    background-color: #c2c2c257;
    outline: none;
}

.resultTable svg{
    cursor:help;
    font-size: 1rem;
    align-items: center;
    color: #1e94f4cc;
}

.result__tablewrapper{
    
    width: 100%;
    height: 60vh;
    overflow: auto;
}

.result__header p{
    margin-bottom: 0;
}

.results__input{
    width: 5vw;
}
