.creategame__modal{
    width: 45vw;
    /* height: 40vh; */
    background-color: #fff;
}

.creategame__modal form{
    height: 23vh;
}

.close-btn{
    right: .5rem;
    top: .5rem;
    background: transparent;
    color: var(--textDark) !important;
}

.close-btn:hover{
    background: transparent !important;
}

.close-btn svg{
    margin: 0;
}

.creategame__modal h2{
    text-align: left;
}

.creategame__modal img{
    width: 20vw;
    height: 35vh;
    object-fit: contain;
}

.createGame__form{
    width: 20vw;
}