.login__container{
    width: 100vw;
    height: 100vh;
    background-color: #1b2845;
    background-image: linear-gradient(315deg, #1b2845 0%, #274060 74%);
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;    
}

.login__card{
    width: 30vw;
    height: 50vh;
    background-color: var(--bgLight);
