}

.login__inputs{
    height: 20vh;
}

.login__container img{
    height: 4rem;
    width: 30vw;
    object-fit: contain;
    top: 17vh;
    right: 50vh;
}

.lp__admin{
    color: var(--bgLight);
    top: 23.4vh;
    right: 58vh;
}