.usersList, .userProfile{
    overflow: auto;
    width: 87vw;
    margin-bottom: 4rem;
}

.userProfile{
    height: 100vh;
}

.users__container, .userProfile__container{
    background-color: var(--bgLight);
    min-height: 80vh;
}

.users__container{
    overflow: scroll;
}

.users__container a{
    color: var(--textDark ) !important;
}

.users__container a:hover{
    background-color: rgba(0, 0, 0, 0.2) !important;
    /* border: 10px solid black; */
}

.userProfile__container{
    min-height: 50vh;
}

.user__header img{
    width: 10rem;
    height: 10rem;
    object-fit: cover;
    border-radius: 50%;
}

.user__header p{
    margin-bottom: 0;
}

.user__stats{
    width: 50vw;
}

.user__header{
    min-height: 50vh;
}

.userStatus__card{
    width: 10vw;
    height: 13vh;
    /* background-color: var(--bgGrey); */
    box-shadow: 5px 8px 13px -2px rgba(0,0,0,0.1);
    transition: .3s ease-in-out;
    /* color: var(--bgLight); */
}

.userStatus__card:hover{
    box-shadow: 5px 8px 13px 4px rgba(0,0,0,0.1);

}

.user__stats div:nth-child(1){
    background-color: #F1E0AC;
    color: #001829;

}

.user__stats div:nth-child(2){
    background-color: #65C18C;
    color: #001829;

}

.user__stats div:nth-child(3){
    background-color: #86C6F4;
    color: #001829;

}

.user__stats div:nth-child(4){
    background-color: #F0A500;
    color: #001829;

}

.user__details{
    min-width: 40vw;
}

.user__head{
    text-align: center;
}

.glc_queue{
    color: #FF9F45;
}

.user__search{
    right: 3rem;
    top:7rem;
    z-index: 11;
}

.user__search input{
    width: 20vw;
    /* transform: translateX(-3rem); */
}

.user__sClrBtn{
    right: 11rem;
    top: 20%;
    background-color: transparent ;
}

.user__sClrBtn svg{
    color: #00000035;
    transition: .3s ease-in-out;
}

.user__sClrBtn:hover svg{
    color: #00000075;
}

.users__name{
    max-width: 3vw !important;
    /* text-decoration: underline; */
}

.users__container a{
    color: var(--navHover) !important;
}

.userT__buttons button {
    box-shadow: 0px -2px 1px -2px rgb(0 0 0 / 14%), 
                0px -2px 2px 0px rgb(0 0 0 / 8%), 
                0px -3px 5px 0px rgb(0 0 0 / 4%);
    transform: translateY(4px);
    background-color: #fff;
}

.userT__buttons button:hover{
    background-color: #fff;
}

.hostings{
    background-color: var(--bgLight);
    width: 90vw;
    height: 90vh;
}

.hosting__closeBtn{
    top: 0;
    right: 0;
}

.hostingdetails{
    background-color: var(--bgLight);
    width: 38vw;
    max-height: 80vh;
    overflow: auto;
}

.hostDetails__card{
    border-top: solid 1px #36363625;
}

.hostDetails__key{
    color: #696969;
    width: 10vw;
}

.details__image{
    width: 100%;
    height: 10rem;
    object-fit: contain;
}